.doc-card {
  background: rgba(15, 23, 42, 0.95);
  border-radius: 1.2rem;
  padding: 1.1rem 1.15rem 1rem;
  border: 1px solid rgba(148, 163, 184, 0.4);
  box-shadow: 0 16px 36px rgba(15, 23, 42, 0.95);
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  min-height: 260px;
}

.doc-header {
  display: flex;
  justify-content: space-between;
  gap: 0.75rem;
  align-items: flex-end;
}

.doc-header h2 {
  margin: 0 0 0.2rem;
  font-size: 1rem;
  color: #f9fafb;
}

.doc-header p {
  margin: 0;
  font-size: 0.8rem;
  color: #9ca3af;
}

.doc-toolbar {
  display: flex;
  gap: 0.45rem;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  padding-bottom: 2px;
  scrollbar-width: thin;
}

.doc-toolbar > * {
  flex: 0 0 auto;
}

.doc-input {
  padding: 0.5rem 0.7rem;
  border-radius: 999px;
  border: 1px solid rgba(148, 163, 184, 0.5);
  background: rgba(15, 23, 42, 0.9);
  color: #e5e7eb;
  font-size: 0.8rem;
  outline: none;
  min-width: 160px;
  box-sizing: border-box;
}

.doc-input::placeholder {
  color: #6b7280;
}

.doc-input:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 1px rgba(37, 99, 235, 0.6);
}

.doc-select {
  padding: 0.5rem 0.7rem;
  border-radius: 999px;
  border: 1px solid rgba(148, 163, 184, 0.5);
  background: rgba(15, 23, 42, 0.9);
  color: #e5e7eb;
  font-size: 0.8rem;
  outline: none;
  box-sizing: border-box;
  min-width: 140px;
  padding-right: 2.2rem;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  background-image: linear-gradient(
      45deg,
      transparent 50%,
      rgba(229, 231, 235, 0.9) 50%
    ),
    linear-gradient(135deg, rgba(229, 231, 235, 0.9) 50%, transparent 50%);
  background-position: calc(100% - 16px) 50%, calc(100% - 11px) 50%;
  background-size: 6px 6px, 6px 6px;
  background-repeat: no-repeat;
}

.doc-select:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 1px rgba(37, 99, 235, 0.6);
}

.doc-btn {
  border-radius: 999px;
  border: 1px solid rgba(148, 163, 184, 0.6);
  padding: 0.28rem 0.7rem;
  font-size: 0.75rem;
  cursor: pointer;
  background: transparent;
  color: #e5e7eb;
  transition: background 0.12s ease, border-color 0.12s ease,
    transform 0.08s ease;
  box-sizing: border-box;
  white-space: nowrap;
}

.doc-btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
}

.doc-btn--ghost:hover {
  background: rgba(37, 99, 235, 0.25);
  border-color: rgba(59, 130, 246, 0.9);
  transform: translateY(-0.5px);
}

.doc-btn--edit {
  background: rgba(15, 23, 42, 0.9);
  border-color: rgba(246, 193, 59, 0.9);
  transform: translateY(-0.5px);
}

.doc-btn--edit:hover {
  background: rgba(235, 219, 37, 0.25);
  border-color: rgba(246, 193, 59, 0.9);
  transform: translateY(-0.5px);
}

.doc-btn--danger {
  border-color: rgba(239, 68, 68, 0.65);
}

.doc-btn--danger:hover {
  background: rgba(239, 68, 68, 0.18);
  border-color: rgba(239, 68, 68, 0.95);
  transform: translateY(-0.5px);
}

.doc-btn--primary {
  background: rgba(37, 99, 235, 0.22);
  border-color: rgba(59, 130, 246, 0.9);
}

.doc-btn--primary:hover {
  background: rgba(37, 99, 235, 1);
  transform: translateY(-0.5px);
}

.doc-toolbar .doc-input,
.doc-toolbar .doc-select,
.doc-toolbar .doc-btn {
  height: 40px;
  display: inline-flex;
  align-items: center;
}

.doc-toolbar .doc-input,
.doc-toolbar .doc-select {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0.85rem;
  padding-right: 0.85rem;
  line-height: 40px;
}

.doc-toolbar .doc-select {
  padding-right: 2.2rem;
}

.doc-toolbar .doc-btn {
  padding: 0 0.95rem;
  line-height: 1;
}

.doc-row {
  display: flex;
  gap: 0.45rem;
  flex-wrap: wrap;
  align-items: center;
}

.doc-row .doc-input,
.doc-row .doc-select,
.doc-row .doc-btn {
  height: 40px;
  display: inline-flex;
  align-items: center;
}

.doc-row .doc-input,
.doc-row .doc-select {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0.85rem;
  padding-right: 0.85rem;
  line-height: 40px;
}

.doc-row .doc-btn {
  padding: 0 0.95rem;
  line-height: 1;
}

.doc-btn--sm {
  height: 30px;
  padding: 0 0.65rem;
  font-size: 0.72rem;
  line-height: 1;
  display: inline-flex;
  align-items: center;
}

.doc-info {
  font-size: 0.85rem;
  padding: 0.3rem 0;
  color: #e5e7eb;
}

.doc-error {
  font-size: 0.85rem;
  padding: 0.3rem 0;
  color: #fecaca;
}

.doc-tablewrap {
  border-radius: 0.9rem;
  border: 1px solid rgba(31, 41, 55, 0.9);
  overflow: hidden;
  background: radial-gradient(
      circle at top left,
      rgba(30, 64, 175, 0.15),
      transparent 60%
    ),
    rgba(15, 23, 42, 0.95);
}

.doc-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.8rem;
  color: #e5e7eb;
}

.doc-table thead {
  background: rgba(15, 23, 42, 0.98);
}

.doc-table th,
.doc-table td {
  padding: 0.55rem 0.65rem;
  text-align: left;
  vertical-align: middle;
}

.doc-table th {
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 0.68rem;
  color: #9ca3af;
  border-bottom: 1px solid rgba(55, 65, 81, 0.9);
}

.doc-table tbody tr:nth-child(odd) {
  background: rgba(15, 23, 42, 0.98);
}

.doc-table tbody tr:nth-child(even) {
  background: rgba(17, 24, 39, 0.98);
}

.doc-table tbody tr:hover {
  background: rgba(30, 64, 175, 0.35);
}

.doc-empty {
  text-align: center;
  padding: 1rem 0.7rem;
  color: #9ca3af;
}

.doc-th-right,
.doc-td-right {
  text-align: right !important;
}

.doc-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.22rem 0.55rem;
  border-radius: 999px;
  font-size: 0.72rem;
  border: 1px solid rgba(148, 163, 184, 0.4);
  letter-spacing: 0.02em;
}

.doc-badge--muted {
  background: rgba(148, 163, 184, 0.12);
  color: #e5e7eb;
}

.doc-badge--ok {
  background: rgba(34, 197, 94, 0.14);
  border-color: rgba(34, 197, 94, 0.35);
  color: #dcfce7;
}

.doc-table th:last-child,
.doc-table td:last-child {
  text-align: right;
  padding-right: 0.9rem;
}
